<p class="tip">在Chrome模拟器运行时你可能会发现没有效果，这是因为Chrome并不支持原生sticky实现而模拟器环境是iPhone，目前程序是根据Safari版本来判断是否原生支持sticky。因此你可以切换到Android系统进行测试，但是真机上是完全正常的交互。
<br>
<br>
你也可以设置禁用原生支持检测以获得相同的效果：`:check-sticky-support="false"`
</p>
<p>如果你没有使用<code>100%</code>的布局，也没有头部可以直接这样使用</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">sticky</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
</code></pre>
<p>如果你像demo一样使用<code>view-box</code>和<code>x-header</code>，那么需要这样：</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">sticky</span> <span class="hljs-attr">scroll-box</span>=<span class="hljs-string">"vux_view_box_body"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"46"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
</code></pre>
<p>建议外围加一个<code>div</code>高度为内容高度，这样可以避免当定位为<code>sticky</code>时下面的元素会突然向上走。</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:44px;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">sticky</span> <span class="hljs-attr">scroll-box</span>=<span class="hljs-string">"vux_view_box_body"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"46"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<p><span class="vux-props-title">Props</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>version</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">scroll-box</span></td>
<td><span class="type type-string">String</span></td>
<td>window</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>滚动容器，默认为<code>window</code>，如果你使用了viewbox，那么你需要指定容器id：<code>vux_view_box_body</code></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">check-sticky-support</span></td>
<td><span class="type type-boolean">Boolean</span></td>
<td>true</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>是否检测当前浏览器是否支持sticky特性，禁用则在<code>iPhone</code>设置上也使用<code>scroll</code>实现</td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">offset</span></td>
<td><span class="type type-number">Number</span></td>
<td>0</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
<td>距离顶部高度，在存在头部（如使用了<code>x-header</code>）的情况下需要设置一个距离</td>
</tr>
</tbody>
</table>
<p><span class="vux-props-title">Slots</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
<th>version</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">default</span></td>
<td>内容插槽</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
</tbody>
</table>
<p><a class="vux-demo-link" href="#" router-link="/zh-CN/demos/sticky">Demo page</a></p>
<hr>
<h3>ViewBox_COM</h3>
<p><span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/components/view-box/metas.yml" target="_blank">编辑文档</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="#" router-link="/zh-CN/demos/view-box">进入demo页面</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://vux.li/demos/v2/#/component/view-box" target="_blank">demo 原始链接</a></span><br>
  <span style="color: #999;font-size:12px;"><a href="https://github.com/airyland/vux/blob/v2/src/demos/ViewBox.vue" target="_blank">demo源码</a></span></p>
<pre class="hljs"><code><span class="hljs-keyword">import</span> { ViewBox } <span class="hljs-keyword">from</span> <span class="hljs-string">'vux'</span>
</code></pre>
<p>该组件为<code>100%高</code>布局，可以解决部分键盘输入的问题，但是同时会在safari中出现向下滚动时无法自动隐藏url工具栏和底部栏的问题。</p>
<p>在<code>viewBox</code>里元素定位为<code>absolute</code>，效果等同于<code>fixed</code>。</p>
<p>使用时需要设置 html, body 高为100%:</p>
<pre class="hljs"><code><span class="hljs-selector-tag">html</span>, <span class="hljs-selector-tag">body</span> {
  <span class="hljs-attribute">height</span>: <span class="hljs-number">100%</span>;
  <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
  <span class="hljs-attribute">overflow-x</span>: hidden;
}
</code></pre>
<p>view-box父div也需要为100%高度：</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:100%;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">view-box</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"viewBox"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">x-header</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"header"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:100%;position:absolute;left:0;top:0;z-index:100;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">x-header</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">router-view</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">router-view</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">tabbar</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"bottom"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">tabbar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">view-box</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<p>如果你想保存滚动距离，推荐使用<code>vuex</code>实现，在特定<code>path</code>对<code>scrollBody</code>监听<code>scroll</code>事件，并获取滚动距离保存到<code>vuex</code>的<code>state</code>里。示例可以参考vux源码的<code>App.vue</code></p>
<p><span class="vux-props-title">Slots</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>description</th>
<th>version</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">header</span></td>
<td>顶部区域，如果要使用统一的XHeader，可以使用该slot</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">default</span></td>
<td>主体内容，可滚动的区域</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">bottom</span></td>
<td>底部区域，Tabbar可以使用该slot</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">body-padding-top</span></td>
<td>主体的<code>padding-top</code>值，当底部存在<code>x-header</code>等<code>absolute</code>定位元素时需要设置</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">body-padding-bottom</span></td>
<td>主体的<code>padding-bottom</code>值，当底部存在<code>tabbar</code>等<code>absolute</code>定位元素时需要设置</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
</tbody>
</table>
<p><span class="vux-props-title">Methods</span></p>
<table>
<thead>
<tr>
<th>name</th>
<th>params</th>
<th>description</th>
<th>version</th>
</tr>
</thead>
<tbody>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">scrollTo</span></td>
<td><code>(top)</code></td>
<td>滚动到指定位置</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">getScrollTop</span></td>
<td> </td>
<td>获取当前滚动距离</td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
<tr>
<td><span class="prop-key" style="white-space:nowrap;">getScrollBody</span></td>
<td> </td>
<td>获取滚动<code>div</code>, 也可以直接用组件引用的<code>.$refs.viewBoxBody</code></td>
<td><span style="font-size:12px;white-space:nowrap;"></span></td>
</tr>
</tbody>
</table>
